@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format(woff2), url("../fonts/ptsans.woff") format(woff);
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format(woff2), url("../fonts/ptsansbold.woff") format(woff);
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
}
.title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
}
@media (min-width: 768px) {
  .title {
    font-size: 21px;
  }
}
.title--light {
  color: #ffffff;
}
.title--large {
  font-size: 21px;
}
@media (min-width: 768px) {
  .title--large {
    font-size: 30px;
  }
}
.description {
  text-transform: uppercase;
  text-align: center;
  margin: 0;
}
.button {
  display: block;
  padding-top: 16px;
  padding-bottom: 16px;
  text-align: center;
  text-transform: uppercase;
  font-family: inherit;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  color: #ffffff;
  text-decoration: none;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #669EC0;
}
.button:not(:disabled):active {
  background-color: #5496BD;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
  cursor: auto;
}
.page {
  height: 100%;
}
.page__container {
  width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
@media (min-width: 768px) {
  .page__container {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .page__container {
    width: 1200px;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}
.page__body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #333333;
  background-color: #f2f2f2;
  margin: 0;
  height: 100%;
}
.main-container {
  flex-grow: 1;
}
.main-header {
  position: relative;
}
.main-header__logo-image {
  display: block;
}
@media (max-width: 767px) {
  .main-nav--opened + .main-header__logo {
    display: none;
  }
  .main-nav--no-js + .main-header__logo {
    bottom: -40px;
  }
}
.main-header__logo {
  position: absolute;
  width: 185px;
  left: 0;
  right: 0;
  margin: auto;
  bottom: -39px;
  z-index: 1;
}
.main-header__logo[href]:hover {
  opacity: 0.6;
}
.main-header__logo[href]:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-header__logo {
    top: 0;
  }
}
.main-nav {
  min-height: 56px;
  position: relative;
}
@media (min-width: 768px) {
  .main-nav {
    padding-bottom: 0;
  }
}
.main-nav__toggle {
  position: absolute;
  display: block;
  top: 17px;
  right: 20px;
  width: 23px;
  height: 22px;
  background-position: center;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 2;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--no-js {
    padding-bottom: 56px;
  }
}
.main-nav--no-js .main-nav__toggle {
  display: none;
}
.main-nav--closed .main-nav__toggle {
  background-image: url("../img/sprite.svg#header_icon-menu");
}
.main-nav--opened .main-nav__toggle {
  background-image: url("../img/sprite.svg#header_icon-cross");
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
.main-nav--opened .main-nav__list {
  position: absolute;
  width: 100%;
  z-index: 1;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    background-color: #ffffff;
  }
}
@media (max-width: 767px) {
  .main-nav__item {
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
    margin-bottom: 1px;
  }
  .main-nav__item:first-child {
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2);
  }
  .main-nav__item:last-child {
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
    position: relative;
    margin-bottom: 0;
  }
  .main-nav__item:last-child::before {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 7px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
    bottom: 0;
  }
}
@media (min-width: 768px) {
  .main-nav__item:nth-child(4n + 2) {
    margin-right: 153px;
  }
}
@media (min-width: 1200px) {
  .main-nav__item:nth-child(4n + 2) {
    margin-right: 240px;
  }
}
.main-nav__link {
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  display: block;
  text-align: center;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 17px;
  padding-bottom: 12px;
}
.main-nav__link[href]:hover {
  background-color: #669EC0;
}
@media (min-width: 768px) {
  .main-nav__link[href]:hover {
    background-color: #ffffff;
    color: #81b3d2;
  }
}
.main-nav__link[href]:active {
  background-color: #5496BD;
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .main-nav__link[href]:active {
    background-color: #ffffff;
    color: rgba(0, 0, 0, 0.3);
  }
}
@media (min-width: 768px) {
  .main-nav__link {
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    color: #000000;
    padding-top: 18px;
    padding-bottom: 17px;
    width: 133px;
  }
  .main-nav__link--current {
    color: #766357;
  }
}
@media (min-width: 1200px) {
  .main-nav__link {
    width: 220px;
  }
}
