.title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: @dark-color;
  text-align: center;
  text-transform: uppercase;
  margin: 0;

  @media (min-width: @tablet-width) {
    font-size: 21px;
  }

  &--light {
    color: @light-color;
  }

  &--large {
    font-size: 21px;

    @media (min-width: @tablet-width) {
      font-size: 30px;
    }
  }
}
